.header-container{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 150px;
    padding-top:0px;
    padding-bottom:0px;
    background-color: var(--secondary-color);
    color:var(--primary-color);
    background-color: rgba(76, 96, 105, 0.8);
    position: fixed;
    top: 0;
    width: 100%;
    box-sizing: border-box;
    z-index: 1000;
    height:100px;
}


.header-container h1{
    font-family: 'Saira Extra Condensed', sans-serif;
    font-weight: 100;
    font-size: 60px;
    letter-spacing: 30px;
    color: white;
    padding:0px;
    margin:0px;
}


.nav{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 50%;
    font-family: 'Saira Extra Condensed', sans-serif;
    font-weight: 500;
    font-size: 30px;
    letter-spacing: 2px;
    color: whitesmoke;
}

a {
    text-decoration: none;
    color: whitesmoke;
    padding: 10px;
    
}

a:hover {
  text-decoration: underline;
}

.title a:hover{
  text-decoration: none;

}



.design-section,
.construction-section,
.about-section,
.studies-section {
    position: relative;
    display: inline-block;
  }


.scroll-links {
  display: none;
  position: absolute;
  top: 80%;
  left: -30%;
  background-color: rgba(76, 96, 105, 0.8);
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.scroll-links .scroll-trigger {
  display: block;
  padding: 12px 16px;
  text-decoration: none;
  color: white;
}

.scroll-links .scroll-trigger:hover {
  background-color: #9d9d9d;
  
}



.studies-section:hover .scroll-links {
  display: block;
}

.design-section:hover .scroll-links {
  display: block;
}

.construction-section:hover .scroll-links {
  display: block;
}

.about-section:hover .scroll-links {
  display: block;
}

.scroll-links .active{
  background-color: #9d9d9d;
  text-decoration: underline;
}

.active{
  text-decoration: underline;

}


